'use client';

import { useState, useEffect } from 'react';
import { notionService } from '@/lib/notion';
import ApiKeyInput from '@/components/ApiKeyInput';
import DatabaseSelector from '@/components/DatabaseSelector';
import DatabaseView from '@/components/DatabaseView';
import PageView from '@/components/PageView';

const STORAGE_KEY = 'notion_api_key';

export default function Home() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [selectedDatabase, setSelectedDatabase] = useState<string | null>(null);
  const [selectedPage, setSelectedPage] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Check for stored API key on mount
    const storedKey = localStorage.getItem(STORAGE_KEY);
    if (storedKey) {
      notionService.initialize(storedKey);
      setIsAuthenticated(true);
    }
    setIsLoading(false);
  }, []);

  const handleApiKeySubmit = (key: string, remember: boolean) => {
    notionService.initialize(key);
    if (remember) {
      localStorage.setItem(STORAGE_KEY, key);
    }
    setIsAuthenticated(true);
  };

  const handleLogout = () => {
    localStorage.removeItem(STORAGE_KEY);
    setIsAuthenticated(false);
    setSelectedDatabase(null);
    setSelectedPage(null);
  };

  const handlePageSelect = (pageId: string) => {
    setSelectedPage(pageId);
  };

  const handleBackToDatabase = () => {
    setSelectedPage(null);
  };

  const handleBackToDatabases = () => {
    setSelectedDatabase(null);
    setSelectedPage(null);
  };

  if (isLoading) {
    return null; // Prevent flash of login screen
  }

  if (!isAuthenticated) {
    return <ApiKeyInput onSubmit={handleApiKeySubmit} />;
  }

  if (selectedPage) {
    return (
      <PageView 
        pageId={selectedPage} 
        onBack={handleBackToDatabase}
        onLogout={handleLogout}
      />
    );
  }

  if (selectedDatabase) {
    return (
      <DatabaseView 
        databaseId={selectedDatabase} 
        onPageSelect={handlePageSelect}
        onBack={handleBackToDatabases}
        onLogout={handleLogout}
      />
    );
  }

  return <DatabaseSelector onSelect={setSelectedDatabase} onLogout={handleLogout} />;
}
