'use client';

interface BlockRendererProps {
  block: any;
}

export default function BlockRenderer({ block }: BlockRendererProps) {
  const renderRichText = (richTextArray: any[]) => {
    if (!richTextArray || richTextArray.length === 0) return null;
    
    return richTextArray.map((text, index) => {
      const { annotations } = text;
      let element = <span key={index}>{text.plain_text}</span>;
      
      if (annotations.bold) element = <strong key={index}>{text.plain_text}</strong>;
      if (annotations.italic) element = <em key={index}>{text.plain_text}</em>;
      if (annotations.strikethrough) element = <del key={index}>{text.plain_text}</del>;
      if (annotations.underline) element = <u key={index}>{text.plain_text}</u>;
      if (annotations.code) element = <code key={index} className="bg-gray-100 dark:bg-gray-700 px-1 py-0.5 rounded">{text.plain_text}</code>;
      
      if (text.href) {
        element = (
          <a key={index} href={text.href} target="_blank" rel="noopener noreferrer" 
             className="text-indigo-600 hover:text-indigo-900 dark:text-indigo-400 dark:hover:text-indigo-300">
            {element}
          </a>
        );
      }
      
      return element;
    });
  };

  switch (block.type) {
    case 'paragraph':
      return (
        <p className="leading-7 min-h-[1.5rem]">
          {renderRichText(block.paragraph.rich_text)}
        </p>
      );
    
    case 'heading_1':
      return (
        <h1 className="text-3xl font-semibold mt-8 mb-2 text-gray-900 dark:text-white">
          {renderRichText(block.heading_1.rich_text)}
        </h1>
      );
    
    case 'heading_2':
      return (
        <h2 className="text-2xl font-semibold mt-6 mb-2 text-gray-900 dark:text-white">
          {renderRichText(block.heading_2.rich_text)}
        </h2>
      );
    
    case 'heading_3':
      return (
        <h3 className="text-xl font-semibold mt-4 mb-1 text-gray-900 dark:text-white">
          {renderRichText(block.heading_3.rich_text)}
        </h3>
      );
    
    case 'bulleted_list_item':
      return (
        <li className="ml-6 list-disc leading-7">
          {renderRichText(block.bulleted_list_item.rich_text)}
        </li>
      );
    
    case 'numbered_list_item':
      return (
        <li className="ml-6 list-decimal leading-7">
          {renderRichText(block.numbered_list_item.rich_text)}
        </li>
      );
    
    case 'to_do':
      return (
        <div className="flex items-start gap-2">
          <input
            type="checkbox"
            checked={block.to_do.checked}
            disabled
            className="mt-1 h-4 w-4 rounded border-gray-300"
          />
          <span className={`leading-7 ${block.to_do.checked ? 'line-through text-gray-500' : ''}`}>
            {renderRichText(block.to_do.rich_text)}
          </span>
        </div>
      );
    
    case 'toggle':
      return (
        <details className="mb-2">
          <summary className="cursor-pointer font-medium">
            {renderRichText(block.toggle.rich_text)}
          </summary>
        </details>
      );
    
    case 'code':
      return (
        <pre className="bg-gray-100 dark:bg-gray-800 p-4 rounded-md my-2 overflow-x-auto">
          <code className="text-sm font-mono">
            {block.code.rich_text[0]?.plain_text || ''}
          </code>
        </pre>
      );
    
    case 'quote':
      return (
        <blockquote className="border-l-3 border-gray-800 dark:border-gray-200 pl-4 my-2">
          {renderRichText(block.quote.rich_text)}
        </blockquote>
      );
    
    case 'divider':
      return <hr className="my-4 border-t border-gray-300 dark:border-gray-700" />;
    
    case 'image':
      const imageUrl = block.image.type === 'external' 
        ? block.image.external.url 
        : block.image.file?.url;
      
      return imageUrl ? (
        <img
          src={imageUrl}
          alt={block.image.caption?.[0]?.plain_text || 'Image'}
          className="max-w-full h-auto rounded-lg mb-4"
        />
      ) : null;
    
    case 'video':
      const videoUrl = block.video.type === 'external' 
        ? block.video.external.url 
        : block.video.file?.url;
      
      return videoUrl ? (
        <div className="mb-4">
          <video controls className="max-w-full h-auto rounded-lg">
            <source src={videoUrl} />
            Your browser does not support the video tag.
          </video>
        </div>
      ) : null;
    
    case 'file':
      const fileUrl = block.file.type === 'external' 
        ? block.file.external.url 
        : block.file.file?.url;
      
      return fileUrl ? (
        <a
          href={fileUrl}
          target="_blank"
          rel="noopener noreferrer"
          className="inline-flex items-center text-indigo-600 hover:text-indigo-900 dark:text-indigo-400 dark:hover:text-indigo-300 mb-4"
        >
          ðŸ“Ž {block.file.caption?.[0]?.plain_text || 'Download file'}
        </a>
      ) : null;
    
    case 'bookmark':
      return (
        <a
          href={block.bookmark.url}
          target="_blank"
          rel="noopener noreferrer"
          className="block p-4 border border-gray-200 dark:border-gray-700 rounded-lg mb-4 hover:border-indigo-500 dark:hover:border-indigo-400"
        >
          <p className="text-indigo-600 dark:text-indigo-400">{block.bookmark.url}</p>
          {block.bookmark.caption?.[0] && (
            <p className="text-sm text-gray-600 dark:text-gray-400 mt-1">
              {block.bookmark.caption[0].plain_text}
            </p>
          )}
        </a>
      );
    
    case 'table_of_contents':
      return (
        <div className="p-4 bg-gray-50 dark:bg-gray-800 rounded-lg mb-4">
          <p className="text-sm text-gray-600 dark:text-gray-400">Table of Contents</p>
        </div>
      );
    
    case 'child_database':
      return (
        <div className="p-4 border border-gray-200 dark:border-gray-700 rounded-lg mb-4">
          <p className="text-gray-600 dark:text-gray-400">
            ðŸ“Š {block.child_database.title || 'Embedded Database'}
          </p>
        </div>
      );
    
    case 'child_page':
      return (
        <div className="p-4 border border-gray-200 dark:border-gray-700 rounded-lg mb-4">
          <p className="text-gray-600 dark:text-gray-400">
            ðŸ“„ {block.child_page.title || 'Subpage'}
          </p>
        </div>
      );
    
    default:
      return (
        <div className="p-4 bg-gray-100 dark:bg-gray-800 rounded mb-4">
          <p className="text-sm text-gray-600 dark:text-gray-400">
            Unsupported block type: {block.type}
          </p>
        </div>
      );
  }
}