'use client';

import { useState } from 'react';
import { X, Plus, Trash2 } from 'lucide-react';

interface FilterConfig {
  column: string;
  type: string;
  operator: string;
  value: any;
}

interface FilterPanelProps {
  isOpen: boolean;
  onClose: () => void;
  columns: string[];
  items: any[];
  filters: FilterConfig[];
  onAddFilter: (filter: FilterConfig) => void;
  onRemoveFilter: (index: number) => void;
  onUpdateFilter: (index: number, filter: FilterConfig) => void;
}

const operatorsByType: Record<string, { value: string; label: string }[]> = {
  text: [
    { value: 'contains', label: 'Contains' },
    { value: 'does_not_contain', label: 'Does not contain' },
    { value: 'is_empty', label: 'Is empty' },
    { value: 'is_not_empty', label: 'Is not empty' },
  ],
  select: [
    { value: 'equals', label: 'Is' },
    { value: 'not_equals', label: 'Is not' },
    { value: 'is_empty', label: 'Is empty' },
    { value: 'is_not_empty', label: 'Is not empty' },
  ],
  checkbox: [
    { value: 'equals', label: 'Is' },
  ],
  number: [
    { value: 'equals', label: 'Equals' },
    { value: 'greater_than', label: 'Greater than' },
    { value: 'less_than', label: 'Less than' },
    { value: 'is_empty', label: 'Is empty' },
    { value: 'is_not_empty', label: 'Is not empty' },
  ],
  date: [
    { value: 'equals', label: 'Is' },
    { value: 'before', label: 'Before' },
    { value: 'after', label: 'After' },
    { value: 'is_empty', label: 'Is empty' },
    { value: 'is_not_empty', label: 'Is not empty' },
  ],
};

export default function FilterPanel({
  isOpen,
  onClose,
  columns,
  items,
  filters,
  onAddFilter,
  onRemoveFilter,
  onUpdateFilter,
}: FilterPanelProps) {
  const [newFilter, setNewFilter] = useState<Partial<FilterConfig>>({});

  const getPropertyType = (column: string): string => {
    if (!items.length) return 'text';
    const property = items[0].properties[column];
    if (!property) return 'text';
    
    switch (property.type) {
      case 'title':
      case 'rich_text':
        return 'text';
      case 'select':
      case 'status':
        return 'select';
      case 'checkbox':
        return 'checkbox';
      case 'number':
        return 'number';
      case 'date':
      case 'created_time':
      case 'last_edited_time':
        return 'date';
      default:
        return 'text';
    }
  };

  const getSelectOptions = (column: string): string[] => {
    const options = new Set<string>();
    items.forEach(item => {
      const property = item.properties[column];
      if (property?.type === 'select' && property.select) {
        options.add(property.select.name);
      }
      if (property?.type === 'status' && property.status) {
        options.add(property.status.name);
      }
    });
    return Array.from(options).sort();
  };

  const handleAddFilter = () => {
    if (newFilter.column && newFilter.operator) {
      onAddFilter({
        column: newFilter.column,
        type: getPropertyType(newFilter.column),
        operator: newFilter.operator,
        value: newFilter.value || '',
      } as FilterConfig);
      setNewFilter({});
    }
  };

  const needsValue = (operator: string): boolean => {
    return !['is_empty', 'is_not_empty'].includes(operator);
  };

  return (
    <>
      <div className={`settings-panel ${isOpen ? 'open' : ''}`}>
        <div className="settings-panel-header">
          <div className="flex items-center justify-between">
            <h3 className="text-lg font-semibold">Filters</h3>
            <button
              onClick={onClose}
              className="p-2 hover:bg-[var(--muted)] rounded-lg transition-colors"
            >
              <X className="h-4 w-4" />
            </button>
          </div>
          <p className="text-sm text-[var(--muted-foreground)] mt-2">
            Filter items based on property values
          </p>
        </div>
        
        <div className="settings-panel-content">
          {/* Existing Filters */}
          {filters.length > 0 && (
            <div className="mb-6 space-y-2">
              <h4 className="text-sm font-medium mb-3">Active Filters</h4>
              {filters.map((filter, index) => (
                <div
                  key={index}
                  className="flex items-center justify-between p-3 bg-[var(--muted)] rounded-lg"
                >
                  <div className="flex-1 text-sm">
                    <span className="font-medium">{filter.column}</span>
                    <span className="mx-2 text-[var(--muted-foreground)]">
                      {operatorsByType[filter.type]?.find(op => op.value === filter.operator)?.label}
                    </span>
                    {needsValue(filter.operator) && (
                      <span className="font-medium">{filter.value}</span>
                    )}
                  </div>
                  <button
                    onClick={() => onRemoveFilter(index)}
                    className="p-1 hover:bg-[var(--border)] rounded transition-colors"
                  >
                    <Trash2 className="h-4 w-4 text-red-500" />
                  </button>
                </div>
              ))}
            </div>
          )}

          {/* Add New Filter */}
          <div className="space-y-4">
            <h4 className="text-sm font-medium">Add Filter</h4>
            
            {/* Column Selection */}
            <div>
              <label className="block text-sm font-medium mb-1">Column</label>
              <select
                value={newFilter.column || ''}
                onChange={(e) => {
                  const column = e.target.value;
                  setNewFilter({ column });
                }}
                className="w-full px-3 py-2 border border-[var(--border)] rounded-lg bg-[var(--card)] focus:outline-none focus:ring-2 focus:ring-[var(--primary)]"
              >
                <option value="">Select a column</option>
                {columns.map(col => (
                  <option key={col} value={col}>{col}</option>
                ))}
              </select>
            </div>

            {/* Operator Selection */}
            {newFilter.column && (
              <div>
                <label className="block text-sm font-medium mb-1">Condition</label>
                <select
                  value={newFilter.operator || ''}
                  onChange={(e) => setNewFilter({ ...newFilter, operator: e.target.value })}
                  className="w-full px-3 py-2 border border-[var(--border)] rounded-lg bg-[var(--card)] focus:outline-none focus:ring-2 focus:ring-[var(--primary)]"
                >
                  <option value="">Select condition</option>
                  {operatorsByType[getPropertyType(newFilter.column)].map(op => (
                    <option key={op.value} value={op.value}>{op.label}</option>
                  ))}
                </select>
              </div>
            )}

            {/* Value Input */}
            {newFilter.column && newFilter.operator && needsValue(newFilter.operator) && (
              <div>
                <label className="block text-sm font-medium mb-1">Value</label>
                {getPropertyType(newFilter.column) === 'select' ? (
                  <select
                    value={newFilter.value || ''}
                    onChange={(e) => setNewFilter({ ...newFilter, value: e.target.value })}
                    className="w-full px-3 py-2 border border-[var(--border)] rounded-lg bg-[var(--card)] focus:outline-none focus:ring-2 focus:ring-[var(--primary)]"
                  >
                    <option value="">Select value</option>
                    {getSelectOptions(newFilter.column).map(option => (
                      <option key={option} value={option}>{option}</option>
                    ))}
                  </select>
                ) : getPropertyType(newFilter.column) === 'checkbox' ? (
                  <select
                    value={newFilter.value || ''}
                    onChange={(e) => setNewFilter({ ...newFilter, value: e.target.value === 'true' })}
                    className="w-full px-3 py-2 border border-[var(--border)] rounded-lg bg-[var(--card)] focus:outline-none focus:ring-2 focus:ring-[var(--primary)]"
                  >
                    <option value="">Select value</option>
                    <option value="true">Checked</option>
                    <option value="false">Unchecked</option>
                  </select>
                ) : getPropertyType(newFilter.column) === 'number' ? (
                  <input
                    type="number"
                    value={newFilter.value || ''}
                    onChange={(e) => setNewFilter({ ...newFilter, value: parseFloat(e.target.value) })}
                    className="w-full px-3 py-2 border border-[var(--border)] rounded-lg bg-[var(--card)] focus:outline-none focus:ring-2 focus:ring-[var(--primary)]"
                    placeholder="Enter number"
                  />
                ) : getPropertyType(newFilter.column) === 'date' ? (
                  <input
                    type="date"
                    value={newFilter.value || ''}
                    onChange={(e) => setNewFilter({ ...newFilter, value: e.target.value })}
                    className="w-full px-3 py-2 border border-[var(--border)] rounded-lg bg-[var(--card)] focus:outline-none focus:ring-2 focus:ring-[var(--primary)]"
                  />
                ) : (
                  <input
                    type="text"
                    value={newFilter.value || ''}
                    onChange={(e) => setNewFilter({ ...newFilter, value: e.target.value })}
                    className="w-full px-3 py-2 border border-[var(--border)] rounded-lg bg-[var(--card)] focus:outline-none focus:ring-2 focus:ring-[var(--primary)]"
                    placeholder="Enter value"
                  />
                )}
              </div>
            )}

            {/* Add Button */}
            <button
              onClick={handleAddFilter}
              disabled={!newFilter.column || !newFilter.operator}
              className="button-primary w-full py-2 disabled:opacity-50 disabled:cursor-not-allowed inline-flex items-center justify-center gap-2"
            >
              <Plus className="h-4 w-4" />
              Add Filter
            </button>
          </div>
        </div>
      </div>

      {/* Overlay */}
      {isOpen && (
        <div
          className="fixed inset-0 bg-black/20 z-40"
          onClick={onClose}
        />
      )}
    </>
  );
}