'use client';

import { useState, useEffect } from 'react';
import { X, ArrowUp, ArrowDown, Settings, Hash, Calendar, CheckSquare, Link, Users, Paperclip } from 'lucide-react';
import { SortConfig, SortLevel, PropertyType } from '@/types/sort';
import { getSuggestedSortOptions, getStatusOptions, getSelectOptions } from '@/lib/sortUtils';

interface SortPanelProps {
  isOpen: boolean;
  onClose: () => void;
  columns: Array<{
    name: string;
    type?: PropertyType;
  }>;
  items: any[];
  currentSort: SortConfig;
  onSort: (sortConfig: SortConfig) => void;
}

export default function SortPanel({
  isOpen,
  onClose,
  columns,
  currentSort,
  onSort,
}: SortPanelProps) {
  const [localSortConfig, setLocalSortConfig] = useState<SortConfig>(currentSort);
  const [showAdvanced, setShowAdvanced] = useState(false);

  useEffect(() => {
    setLocalSortConfig(currentSort);
  }, [currentSort]);

  const handleQuickSort = (columnName: string, propertyType?: PropertyType) => {
    const existingLevel = localSortConfig.levels.find(l => l.column === columnName && l.enabled);
    
    if (existingLevel) {
      // Toggle direction
      const newLevels = localSortConfig.levels.map(l => 
        l.column === columnName 
          ? { ...l, direction: l.direction === 'asc' ? 'desc' : 'asc' as 'asc' | 'desc' }
          : l
      );
      const newConfig = { ...localSortConfig, levels: newLevels };
      setLocalSortConfig(newConfig);
      onSort(newConfig);
    } else {
      // Create new sort level with field-type-specific defaults
      const suggestedOptions = propertyType ? getSuggestedSortOptions(propertyType, items, columnName) : {};
      const newLevel: SortLevel = {
        id: Date.now().toString(),
        column: columnName,
        direction: 'asc',
        enabled: true,
        nullHandling: 'last',
        ...suggestedOptions
      };
      const newConfig = { ...localSortConfig, levels: [newLevel] };
      setLocalSortConfig(newConfig);
      onSort(newConfig);
    }
  };

  const getPropertyIcon = (type?: PropertyType) => {
    switch (type) {
      case 'number':
      case 'formula':
      case 'rollup':
        return <Hash className="h-3.5 w-3.5" />;
      case 'date':
      case 'created_time':
      case 'last_edited_time':
        return <Calendar className="h-3.5 w-3.5" />;
      case 'checkbox':
        return <CheckSquare className="h-3.5 w-3.5" />;
      case 'relation':
        return <Link className="h-3.5 w-3.5" />;
      case 'people':
      case 'created_by':
      case 'last_edited_by':
        return <Users className="h-3.5 w-3.5" />;
      case 'files':
        return <Paperclip className="h-3.5 w-3.5" />;
      default:
        return null;
    }
  };

  const clearSort = () => {
    const newConfig = { ...localSortConfig, levels: [] };
    setLocalSortConfig(newConfig);
    onSort(newConfig);
  };

  const getSortPreview = (column: { name: string; type?: PropertyType }, isAsc?: boolean, activeLevel?: SortLevel) => {
    const type = column.type;
    if (!type) return isAsc ? 'A â†’ Z' : 'Z â†’ A';
    
    // Check if this is a status/select with custom order
    if ((type === 'status' || type === 'select') && activeLevel?.customOrder && activeLevel.customOrder.length > 0) {
      const orderPreview = activeLevel.customOrder.slice(0, 2).join(' â†’ ');
      const suffix = activeLevel.customOrder.length > 2 ? '...' : '';
      return isAsc ? `${orderPreview}${suffix}` : `â† Custom`;
    }
    
    switch (type) {
      case 'number':
      case 'rollup':
      case 'formula':
        return isAsc ? '1 â†’ 9' : '9 â†’ 1';
      case 'date':
      case 'created_time':
      case 'last_edited_time':
        return isAsc ? 'Old â†’ New' : 'New â†’ Old';
      case 'checkbox':
        return isAsc ? 'â˜ â†’ â˜‘' : 'â˜‘ â†’ â˜';
      case 'status':
        return isAsc ? 'Status order' : 'Status order â†“';
      case 'select':
        return isAsc ? 'Select order' : 'Select order â†“';
      case 'multi_select':
      case 'files':
      case 'relation':
        return isAsc ? 'Few â†’ Many' : 'Many â†’ Few';
      default:
        return isAsc ? 'A â†’ Z' : 'Z â†’ A';
    }
  };

  return (
    <>
      <div className={`settings-panel ${isOpen ? 'open' : ''}`}>
        <div className="settings-panel-header">
          <div className="flex items-center justify-between">
            <h3 className="text-lg font-semibold">Sort</h3>
            <button
              onClick={onClose}
              className="p-2 hover:bg-[var(--muted)] rounded-lg transition-colors"
            >
              <X className="h-4 w-4" />
            </button>
          </div>
          <p className="text-sm text-[var(--muted-foreground)] mt-2">
            Sort items by column values
          </p>
        </div>
        
        <div className="settings-panel-content">
          {/* Current Sort */}
          {localSortConfig.levels.filter(l => l.enabled).length > 0 && (
            <div className="mb-6 p-4 bg-[var(--muted)] rounded-lg">
              <h4 className="text-sm font-medium mb-2">Currently sorted by</h4>
              {localSortConfig.levels
                .filter(l => l.enabled)
                .map((level, index) => (
                  <div key={level.id} className="flex items-center justify-between mb-2">
                    <div className="flex items-center gap-2">
                      {index > 0 && <span className="text-xs text-[var(--muted-foreground)]">then by</span>}
                      <span className="font-medium">{level.column}</span>
                      {level.direction === 'asc' ? (
                        <ArrowUp className="h-4 w-4 text-[var(--primary)]" />
                      ) : (
                        <ArrowDown className="h-4 w-4 text-[var(--primary)]" />
                      )}
                      <span className="text-sm text-[var(--muted-foreground)]">
                        {level.direction === 'asc' ? 'Ascending' : 'Descending'}
                      </span>
                    </div>
                  </div>
                ))}
              <button
                onClick={clearSort}
                className="text-sm text-red-500 hover:text-red-600 mt-2"
              >
                Clear all
              </button>
            </div>
          )}

          {/* Quick Sort Options */}
          <div className="space-y-2">
            <h4 className="text-sm font-medium mb-3">Quick sort</h4>
            {columns.map(column => {
              const activeLevel = localSortConfig.levels.find(l => l.column === column.name && l.enabled);
              const isActive = !!activeLevel;
              const isAsc = activeLevel?.direction === 'asc';
              
              return (
                <button
                  key={column.name}
                  onClick={() => handleQuickSort(column.name, column.type)}
                  className={`w-full text-left p-3 rounded-lg border transition-all ${
                    isActive 
                      ? 'border-[var(--primary)] bg-[var(--primary)]/10' 
                      : 'border-[var(--border)] hover:border-[var(--primary)]/50'
                  }`}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                      {getPropertyIcon(column.type)}
                      <span className={`font-medium ${isActive ? 'text-[var(--primary)]' : ''}`}>
                        {column.name}
                      </span>
                      {column.type && (
                        <span className="text-xs text-[var(--muted-foreground)]">
                          {column.type.replace(/_/g, ' ')}
                        </span>
                      )}
                    </div>
                    <div className="flex items-center gap-2">
                      {isActive && (
                        <span className="text-xs text-[var(--primary)]">
                          {getSortPreview(column, isAsc, activeLevel)}
                        </span>
                      )}
                      {isActive ? (
                        isAsc ? (
                          <ArrowUp className="h-4 w-4 text-[var(--primary)]" />
                        ) : (
                          <ArrowDown className="h-4 w-4 text-[var(--primary)]" />
                        )
                      ) : (
                        <ArrowUp className="h-4 w-4 opacity-30" />
                      )}
                    </div>
                  </div>
                </button>
              );
            })}
          </div>

          {/* Advanced Options */}
          {localSortConfig.levels.filter(l => l.enabled).length > 0 && (
            <div className="mt-8 pt-8 border-t border-[var(--border)]">
              <button
                onClick={() => setShowAdvanced(!showAdvanced)}
                className="flex items-center gap-2 text-sm font-medium text-[var(--muted-foreground)] hover:text-[var(--foreground)] transition-colors"
              >
                <Settings className="h-4 w-4" />
                Advanced options
                <ArrowDown className={`h-3 w-3 transition-transform ${showAdvanced ? 'rotate-180' : ''}`} />
              </button>
              
              {showAdvanced && (
                <div className="mt-4 space-y-4">
                  {localSortConfig.levels
                    .filter(l => l.enabled)
                    .map((level) => {
                      const column = columns.find(c => c.name === level.column);
                      const propertyType = column?.type;
                      
                      return (
                        <div key={level.id} className="p-4 bg-[var(--muted)] rounded-lg">
                          <h5 className="text-sm font-medium mb-3 flex items-center gap-2">
                            {getPropertyIcon(propertyType)}
                            {level.column}
                          </h5>
                          
                          {/* Field-type-specific options */}
                          {(propertyType === 'status' || propertyType === 'select') && (
                            <div className="mb-4">
                              <label className="text-sm text-[var(--muted-foreground)] mb-2 block">Custom order</label>
                              <div className="space-y-2">
                                {(() => {
                                  const options = propertyType === 'status' 
                                    ? getStatusOptions(items, level.column)
                                    : getSelectOptions(items, level.column);
                                  
                                  if (options.length === 0) {
                                    return (
                                      <p className="text-xs text-[var(--muted-foreground)] italic">
                                        No {propertyType} values found
                                      </p>
                                    );
                                  }
                                  
                                  // If customOrder is not set, use the detected order
                                  const currentOrder = level.customOrder?.length > 0 
                                    ? level.customOrder 
                                    : options;
                                  
                                  return (
                                    <>
                                      <p className="text-xs text-[var(--muted-foreground)] mb-2">
                                        Drag to reorder how {propertyType} values are sorted
                                      </p>
                                      <div className="bg-[var(--background)] p-2 rounded-md border border-[var(--border)] space-y-1">
                                        {currentOrder.map((option, index) => (
                                          <div
                                            key={option}
                                            className="flex items-center gap-2 px-2 py-1 text-sm bg-[var(--card)] rounded border border-[var(--border)]"
                                          >
                                            <span className="text-xs text-[var(--muted-foreground)] w-6">{index + 1}.</span>
                                            <span>{option}</span>
                                          </div>
                                        ))}
                                      </div>
                                      <button
                                        onClick={() => {
                                          // Reset to detected order
                                          const newLevels = localSortConfig.levels.map(l =>
                                            l.id === level.id ? { ...l, customOrder: options } : l
                                          );
                                          const newConfig = { ...localSortConfig, levels: newLevels };
                                          setLocalSortConfig(newConfig);
                                          onSort(newConfig);
                                        }}
                                        className="text-xs text-[var(--primary)] hover:underline"
                                      >
                                        Reset to default order
                                      </button>
                                    </>
                                  );
                                })()}
                              </div>
                            </div>
                          )}
                          
                          {(propertyType === 'title' || propertyType === 'rich_text') && (
                            <div className="space-y-2">
                              <label className="flex items-center gap-2">
                                <input
                                  type="checkbox"
                                  checked={level.naturalSort ?? true}
                                  onChange={(e) => {
                                    const newLevels = localSortConfig.levels.map(l =>
                                      l.id === level.id ? { ...l, naturalSort: e.target.checked } : l
                                    );
                                    const newConfig = { ...localSortConfig, levels: newLevels };
                                    setLocalSortConfig(newConfig);
                                    onSort(newConfig);
                                  }}
                                  className="checkbox-modern"
                                />
                                <span className="text-sm">Natural sort (handles numbers in text)</span>
                              </label>
                              <label className="flex items-center gap-2">
                                <input
                                  type="checkbox"
                                  checked={level.caseSensitive ?? false}
                                  onChange={(e) => {
                                    const newLevels = localSortConfig.levels.map(l =>
                                      l.id === level.id ? { ...l, caseSensitive: e.target.checked } : l
                                    );
                                    const newConfig = { ...localSortConfig, levels: newLevels };
                                    setLocalSortConfig(newConfig);
                                    onSort(newConfig);
                                  }}
                                  className="checkbox-modern"
                                />
                                <span className="text-sm">Case sensitive</span>
                              </label>
                            </div>
                          )}
                          
                          {propertyType === 'number' && (
                            <label className="flex items-center gap-2">
                              <input
                                type="checkbox"
                                checked={level.absoluteValue ?? false}
                                onChange={(e) => {
                                  const newLevels = localSortConfig.levels.map(l =>
                                    l.id === level.id ? { ...l, absoluteValue: e.target.checked } : l
                                  );
                                  const newConfig = { ...localSortConfig, levels: newLevels };
                                  setLocalSortConfig(newConfig);
                                  onSort(newConfig);
                                }}
                                className="checkbox-modern"
                              />
                              <span className="text-sm">Sort by absolute value</span>
                            </label>
                          )}
                          
                          {(propertyType === 'date' || propertyType === 'created_time' || propertyType === 'last_edited_time') && (
                            <div>
                              <label className="text-sm text-[var(--muted-foreground)] mb-1">Group by</label>
                              <select
                                value={level.dateGrouping || 'none'}
                                onChange={(e) => {
                                  const newLevels = localSortConfig.levels.map(l =>
                                    l.id === level.id ? { ...l, dateGrouping: e.target.value as any } : l
                                  );
                                  const newConfig = { ...localSortConfig, levels: newLevels };
                                  setLocalSortConfig(newConfig);
                                  onSort(newConfig);
                                }}
                                className="w-full p-2 bg-[var(--background)] border border-[var(--border)] rounded-md text-sm"
                              >
                                <option value="none">No grouping</option>
                                <option value="today">Today/Not today</option>
                                <option value="this_week">This week/Not this week</option>
                                <option value="this_month">This month/Not this month</option>
                                <option value="day_of_week">Day of week</option>
                                <option value="time_only">Time only</option>
                              </select>
                            </div>
                          )}
                        </div>
                      );
                    })}
                </div>
              )}
            </div>
          )}
        </div>
      </div>

      {/* Overlay */}
      {isOpen && (
        <div
          className="fixed inset-0 bg-black/20 z-40"
          onClick={onClose}
        />
      )}
    </>
  );
}