import { SortLevel, SortConfig, PropertyType } from '@/types/sort';

// Natural sort comparator for handling numbers in strings properly
export function naturalCompare(a: string, b: string, caseSensitive: boolean = false): number {
  const ax: any[] = [];
  const bx: any[] = [];
  
  if (!caseSensitive) {
    a = a.toLowerCase();
    b = b.toLowerCase();
  }
  
  a.replace(/(\d+)|(\D+)/g, (_, $1, $2) => {
    ax.push([$1 || Infinity, $2 || ""]);
    return "";
  });
  
  b.replace(/(\d+)|(\D+)/g, (_, $1, $2) => {
    bx.push([$1 || Infinity, $2 || ""]);
    return "";
  });
  
  while (ax.length && bx.length) {
    const an = ax.shift()!;
    const bn = bx.shift()!;
    const nn = (an[0] - bn[0]) || an[1].localeCompare(bn[1]);
    if (nn) return nn;
  }
  
  return ax.length - bx.length;
}

// Get text content from a property
export function getTextContent(property: any): string {
  if (!property) return '';
  
  switch (property.type) {
    case 'title':
      return property.title?.[0]?.plain_text || '';
    case 'rich_text':
      return property.rich_text?.[0]?.plain_text || '';
    case 'select':
      return property.select?.name || '';
    case 'status':
      return property.status?.name || '';
    case 'multi_select':
      return property.multi_select?.map((s: any) => s.name).join(', ') || '';
    case 'email':
      return property.email || '';
    case 'phone_number':
      return property.phone_number || '';
    case 'url':
      return property.url || '';
    case 'people':
      return property.people?.map((p: any) => p.name || p.person?.email || '').join(', ') || '';
    case 'created_by':
      return property.created_by?.name || property.created_by?.person?.email || '';
    case 'last_edited_by':
      return property.last_edited_by?.name || property.last_edited_by?.person?.email || '';
    default:
      return '';
  }
}

// Get numeric value from a property
export function getNumericValue(property: any): number | null {
  if (!property) return null;
  
  switch (property.type) {
    case 'number':
      return property.number;
    case 'checkbox':
      return property.checkbox ? 1 : 0;
    case 'multi_select':
      return property.multi_select?.length || 0;
    case 'people':
      return property.people?.length || 0;
    case 'files':
      return property.files?.length || 0;
    default:
      return null;
  }
}

// Get date value from a property
export function getDateValue(property: any): Date | null {
  if (!property) return null;
  
  switch (property.type) {
    case 'date':
      return property.date?.start ? new Date(property.date.start) : null;
    case 'created_time':
      return new Date(property.created_time);
    case 'last_edited_time':
      return new Date(property.last_edited_time);
    default:
      return null;
  }
}

// Apply date grouping for sorting
export function applyDateGrouping(date: Date | null, grouping: string): number {
  if (!date) return 0;
  
  const now = new Date();
  const dayStart = new Date(now.getFullYear(), now.getMonth(), now.getDate());
  
  switch (grouping) {
    case 'today':
      return date >= dayStart ? 1 : 0;
    case 'this_week':
      const weekStart = new Date(dayStart);
      weekStart.setDate(weekStart.getDate() - weekStart.getDay());
      return date >= weekStart ? 1 : 0;
    case 'this_month':
      const monthStart = new Date(now.getFullYear(), now.getMonth(), 1);
      return date >= monthStart ? 1 : 0;
    case 'day_of_week':
      return date.getDay();
    case 'time_only':
      return date.getHours() * 60 + date.getMinutes();
    default:
      return date.getTime();
  }
}

// Get computed value for sorting
export function getComputedValue(property: any, computedType: string): number {
  const text = getTextContent(property);
  
  switch (computedType) {
    case 'text_length':
      return text.length;
    case 'word_count':
      return text.trim() ? text.trim().split(/\s+/).length : 0;
    case 'selection_count':
      if (property?.type === 'multi_select') {
        return property.multi_select?.length || 0;
      }
      return 0;
    default:
      return 0;
  }
}

// Compare two values based on sort level configuration
export function compareValues(aVal: any, bVal: any, sortLevel: SortLevel, propertyType?: PropertyType): number {
  // Handle null/empty values
  const aIsEmpty = aVal === null || aVal === undefined || aVal === '';
  const bIsEmpty = bVal === null || bVal === undefined || bVal === '';
  
  if (aIsEmpty && bIsEmpty) return 0;
  
  if (sortLevel.nullHandling === 'ignore') {
    if (aIsEmpty && bIsEmpty) return 0;
    if (aIsEmpty || bIsEmpty) return 0;
  } else {
    if (aIsEmpty) return sortLevel.nullHandling === 'first' ? -1 : 1;
    if (bIsEmpty) return sortLevel.nullHandling === 'first' ? 1 : -1;
  }
  
  let comparison = 0;
  
  // Apply computed values if specified
  if (sortLevel.computedValue) {
    aVal = getComputedValue(aVal, sortLevel.computedValue);
    bVal = getComputedValue(bVal, sortLevel.computedValue);
  }
  
  // Type-specific comparison
  if (typeof aVal === 'string' && typeof bVal === 'string') {
    // Handle custom order for select/status fields
    if (sortLevel.customOrder && sortLevel.customOrder.length > 0) {
      const aIndex = sortLevel.customOrder.indexOf(aVal);
      const bIndex = sortLevel.customOrder.indexOf(bVal);
      
      if (aIndex !== -1 && bIndex !== -1) {
        comparison = aIndex - bIndex;
      } else if (aIndex !== -1) {
        comparison = -1;
      } else if (bIndex !== -1) {
        comparison = 1;
      } else {
        // Fall back to natural comparison
        comparison = sortLevel.naturalSort
          ? naturalCompare(aVal, bVal, sortLevel.caseSensitive)
          : sortLevel.caseSensitive
            ? aVal.localeCompare(bVal)
            : aVal.toLowerCase().localeCompare(bVal.toLowerCase());
      }
    } else {
      // Regular string comparison
      comparison = sortLevel.naturalSort
        ? naturalCompare(aVal, bVal, sortLevel.caseSensitive)
        : sortLevel.caseSensitive
          ? aVal.localeCompare(bVal)
          : aVal.toLowerCase().localeCompare(bVal.toLowerCase());
    }
  } else if (typeof aVal === 'number' && typeof bVal === 'number') {
    // Number comparison with absolute value option
    if (sortLevel.absoluteValue) {
      comparison = Math.abs(aVal) - Math.abs(bVal);
    } else {
      comparison = aVal - bVal;
    }
  } else if (aVal instanceof Date && bVal instanceof Date) {
    // Date comparison with grouping
    if (sortLevel.dateGrouping && sortLevel.dateGrouping !== 'none') {
      const aGrouped = applyDateGrouping(aVal, sortLevel.dateGrouping);
      const bGrouped = applyDateGrouping(bVal, sortLevel.dateGrouping);
      comparison = aGrouped - bGrouped;
    } else {
      comparison = aVal.getTime() - bVal.getTime();
    }
  } else {
    // Fallback comparison
    comparison = String(aVal).localeCompare(String(bVal));
  }
  
  // Apply sort direction
  return sortLevel.direction === 'asc' ? comparison : -comparison;
}

// Main sorting function
export function sortItems(items: any[], sortConfig: SortConfig): any[] {
  if (!sortConfig.levels || sortConfig.levels.length === 0) {
    return items;
  }
  
  const enabledLevels = sortConfig.levels.filter(level => level.enabled);
  if (enabledLevels.length === 0) {
    return items;
  }
  
  return [...items].sort((a, b) => {
    for (const level of enabledLevels) {
      const aProperty = a.properties[level.column];
      const bProperty = b.properties[level.column];
      
      // Use the new getSortValue function for consistent value extraction
      const aVal = getSortValue(aProperty, level);
      const bVal = getSortValue(bProperty, level);
      
      const comparison = compareValues(aVal, bVal, level, aProperty?.type);
      if (comparison !== 0) {
        return comparison;
      }
    }
    
    return 0;
  });
}

// Detect property type from items
export function detectPropertyType(items: any[], columnName: string): PropertyType | undefined {
  for (const item of items) {
    const property = item.properties[columnName];
    if (property && property.type) {
      return property.type as PropertyType;
    }
  }
  return undefined;
}

// Get status options from items (preserves original order from Notion)
export function getStatusOptions(items: any[], columnName: string): string[] {
  const statusOptionsMap = new Map<string, number>();
  const statusOptionsWithColors = new Map<string, any>();
  
  // Collect all unique status values with their first occurrence index
  items.forEach((item, index) => {
    const property = item.properties[columnName];
    if (property?.type === 'status' && property.status) {
      const statusName = property.status.name;
      if (!statusOptionsMap.has(statusName)) {
        statusOptionsMap.set(statusName, index);
        statusOptionsWithColors.set(statusName, property.status);
      }
    }
  });
  
  // Sort by first occurrence to approximate original order
  // In a real Notion database, statuses have a defined order
  // This is our best approximation without access to database schema
  return Array.from(statusOptionsMap.entries())
    .sort((a, b) => a[1] - b[1])
    .map(([name]) => name);
}

// Get select options from items (preserves order)
export function getSelectOptions(items: any[], columnName: string): string[] {
  const selectOptionsMap = new Map<string, number>();
  
  items.forEach((item, index) => {
    const property = item.properties[columnName];
    if (property?.type === 'select' && property.select) {
      const selectName = property.select.name;
      if (!selectOptionsMap.has(selectName)) {
        selectOptionsMap.set(selectName, index);
      }
    }
  });
  
  return Array.from(selectOptionsMap.entries())
    .sort((a, b) => a[1] - b[1])
    .map(([name]) => name);
}

// Get formula value for sorting
export function getFormulaValue(property: any): any {
  if (!property || property.type !== 'formula') return null;
  
  const formula = property.formula;
  if (!formula) return null;
  
  switch (formula.type) {
    case 'string':
      return formula.string || '';
    case 'number':
      return formula.number;
    case 'boolean':
      return formula.boolean ? 1 : 0;
    case 'date':
      return formula.date?.start ? new Date(formula.date.start) : null;
    default:
      return null;
  }
}

// Get rollup value for sorting
export function getRollupValue(property: any): any {
  if (!property || property.type !== 'rollup') return null;
  
  const rollup = property.rollup;
  if (!rollup) return null;
  
  switch (rollup.type) {
    case 'number':
      return rollup.number;
    case 'date':
      return rollup.date?.start ? new Date(rollup.date.start) : null;
    case 'array':
      // For array rollups, return count or first value
      if (rollup.array && rollup.array.length > 0) {
        const firstItem = rollup.array[0];
        if (firstItem.type === 'number') return firstItem.number;
        if (firstItem.type === 'date') return new Date(firstItem.date?.start || 0);
        if (firstItem.type === 'string') return firstItem.string || '';
      }
      return rollup.array?.length || 0;
    default:
      return null;
  }
}

// Get relation value for sorting (count of relations)
export function getRelationValue(property: any): number {
  if (!property || property.type !== 'relation') return 0;
  return property.relation?.length || 0;
}

// Enhanced sort value extraction based on property type
export function getSortValue(property: any, sortLevel: SortLevel): any {
  if (!property) return null;
  
  // Apply computed values first if specified
  if (sortLevel.computedValue) {
    return getComputedValue(property, sortLevel.computedValue);
  }
  
  // Extract value based on property type
  switch (property.type) {
    case 'title':
    case 'rich_text':
    case 'select':
    case 'status':
    case 'email':
    case 'phone_number':
    case 'url':
    case 'people':
    case 'created_by':
    case 'last_edited_by':
      return getTextContent(property);
      
    case 'number':
    case 'checkbox':
      return getNumericValue(property);
      
    case 'date':
    case 'created_time':
    case 'last_edited_time':
      return getDateValue(property);
      
    case 'multi_select':
      // For multi-select, we can sort by count or concatenated names
      if (sortLevel.computedValue === 'selection_count') {
        return property.multi_select?.length || 0;
      }
      return getTextContent(property);
      
    case 'files':
      return property.files?.length || 0;
      
    case 'formula':
      return getFormulaValue(property);
      
    case 'rollup':
      return getRollupValue(property);
      
    case 'relation':
      return getRelationValue(property);
      
    default:
      return getTextContent(property);
  }
}

// Get suggested sort options based on property type
export function getSuggestedSortOptions(propertyType: PropertyType, items?: any[], columnName?: string): Partial<SortLevel> {
  switch (propertyType) {
    case 'number':
    case 'rollup':
      return {
        naturalSort: false,
        absoluteValue: false,
        nullHandling: 'last' as NullHandling,
      };
      
    case 'date':
    case 'created_time':
    case 'last_edited_time':
      return {
        dateGrouping: 'none',
        nullHandling: 'last' as NullHandling,
      };
      
    case 'checkbox':
      return {
        direction: 'desc' as SortDirection, // True values first
        nullHandling: 'last' as NullHandling,
      };
      
    case 'status':
      // For status fields, try to detect and use the predefined order
      if (items && columnName) {
        const statusOptions = getStatusOptions(items, columnName);
        if (statusOptions.length > 0) {
          return {
            customOrder: statusOptions,
            caseSensitive: false,
            nullHandling: 'last' as NullHandling,
          };
        }
      }
      return {
        customOrder: [],
        caseSensitive: false,
        nullHandling: 'last' as NullHandling,
      };
      
    case 'select':
      // For select fields, try to detect the order
      if (items && columnName) {
        const selectOptions = getSelectOptions(items, columnName);
        if (selectOptions.length > 0) {
          return {
            customOrder: selectOptions,
            caseSensitive: false,
            nullHandling: 'last' as NullHandling,
          };
        }
      }
      return {
        customOrder: [],
        caseSensitive: false,
        nullHandling: 'last' as NullHandling,
      };
      
    case 'multi_select':
      return {
        computedValue: 'selection_count',
        nullHandling: 'last' as NullHandling,
      };
      
    case 'title':
    case 'rich_text':
      return {
        naturalSort: true,
        caseSensitive: false,
        nullHandling: 'last' as NullHandling,
      };
      
    case 'people':
    case 'created_by':
    case 'last_edited_by':
      return {
        caseSensitive: false,
        nullHandling: 'last' as NullHandling,
      };
      
    case 'files':
    case 'relation':
      return {
        direction: 'desc' as SortDirection, // More items first
        nullHandling: 'last' as NullHandling,
      };
      
    default:
      return {
        naturalSort: true,
        caseSensitive: false,
        nullHandling: 'last' as NullHandling,
      };
  }
}