'use client';

import { useState, useEffect } from 'react';
import { notionService } from '@/lib/notion';
import { ArrowLeft, Loader2, LogOut } from 'lucide-react';
import BlockRenderer from './BlockRenderer';
import PropertyRenderer from './PropertyRenderer';

interface PageViewProps {
  pageId: string;
  onBack: () => void;
  onLogout: () => void;
}

export default function PageView({ pageId, onBack, onLogout }: PageViewProps) {
  const [page, setPage] = useState<any>(null);
  const [blocks, setBlocks] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    loadPageData();
  }, [pageId]);

  const loadPageData = async () => {
    try {
      setLoading(true);
      const [pageData, pageBlocks] = await Promise.all([
        notionService.getPage(pageId),
        notionService.getBlocks(pageId)
      ]);
      setPage(pageData);
      setBlocks(pageBlocks);
    } catch (err) {
      setError('Failed to load page.');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-white dark:bg-gray-900">
        <Loader2 className="h-8 w-8 animate-spin text-gray-400" />
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-white dark:bg-gray-900">
        <div className="text-center">
          <p className="text-red-600 dark:text-red-400">{error}</p>
        </div>
      </div>
    );
  }

  const getPageTitle = () => {
    if (!page?.properties) return 'Untitled';
    const titleProp = Object.values(page.properties).find((prop: any) => prop.type === 'title') as any;
    return titleProp?.title?.[0]?.plain_text || 'Untitled';
  };

  const getPageIcon = () => {
    if (page?.icon?.type === 'emoji') return page.icon.emoji;
    return null;
  };

  const getCoverUrl = () => {
    if (page?.cover?.type === 'external') return page.cover.external.url;
    if (page?.cover?.type === 'file') return page.cover.file.url;
    return null;
  };

  return (
    <div className="min-h-screen bg-white dark:bg-gray-900">
      {/* Cover Image */}
      {getCoverUrl() && (
        <div className="relative h-60 w-full">
          <img 
            src={getCoverUrl()} 
            alt="Page cover" 
            className="w-full h-full object-cover"
          />
          <div className="absolute inset-0 bg-gradient-to-b from-transparent to-black/10" />
        </div>
      )}

      <div className="notion-page py-8">
        <div className="mb-6 flex items-center justify-between">
          <button
            onClick={onBack}
            className="inline-flex items-center text-sm text-gray-600 hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-200 transition-colors"
          >
            <ArrowLeft className="h-4 w-4 mr-1" />
            Back to database
          </button>
          <button
            onClick={onLogout}
            className="button-secondary px-4 py-2 inline-flex items-center gap-2"
          >
            <LogOut className="h-4 w-4" />
            Logout
          </button>
        </div>

        <article className="pb-12">
          {/* Page Title with Icon */}
          <div className="mb-8">
            <div className="flex items-center gap-4 mb-8">
              {getPageIcon() && (
                <span className="text-5xl">{getPageIcon()}</span>
              )}
              <h1 className="text-4xl font-bold text-gray-900 dark:text-white">
                {getPageTitle()}
              </h1>
            </div>

            {/* Page Properties */}
            {page?.properties && Object.keys(page.properties).length > 1 && (
              <div className="bg-gray-50 dark:bg-gray-900/50 rounded-lg p-6 mb-8">
                <div className="grid gap-4">
                  {Object.entries(page.properties)
                    .filter(([_, prop]: [string, any]) => prop.type !== 'title')
                    .filter(([_, prop]: [string, any]) => {
                      // Filter out empty properties
                      if (prop.type === 'rich_text' && (!prop.rich_text || prop.rich_text.length === 0)) return false;
                      if (prop.type === 'number' && prop.number === null) return false;
                      if (prop.type === 'select' && !prop.select) return false;
                      if (prop.type === 'multi_select' && (!prop.multi_select || prop.multi_select.length === 0)) return false;
                      if (prop.type === 'date' && !prop.date) return false;
                      if (prop.type === 'people' && (!prop.people || prop.people.length === 0)) return false;
                      if (prop.type === 'files' && (!prop.files || prop.files.length === 0)) return false;
                      if (prop.type === 'url' && !prop.url) return false;
                      if (prop.type === 'email' && !prop.email) return false;
                      if (prop.type === 'phone_number' && !prop.phone_number) return false;
                      return true;
                    })
                    .map(([key, property]: [string, any]) => (
                      <div key={key} className="flex items-start gap-4">
                        <span className="text-sm font-medium text-gray-600 dark:text-gray-400 min-w-[140px]">
                          {key}
                        </span>
                        <div className="flex-1">
                          <PropertyRenderer property={property} />
                        </div>
                      </div>
                    ))}
                </div>
              </div>
            )}
          </div>

          {/* Page Content */}
          <div className="notion-content space-y-2">
            {blocks.map((block) => (
              <BlockRenderer key={block.id} block={block} />
            ))}
          </div>
        </article>
      </div>
    </div>
  );
}