'use client';

import { useState, useEffect } from 'react';
import { notionService } from '@/lib/notion';
import { Database, Loader2, Calendar, Clock, ChevronRight, LogOut } from 'lucide-react';

interface DatabaseSelectorProps {
  onSelect: (databaseId: string) => void;
  onLogout: () => void;
}

export default function DatabaseSelector({ onSelect, onLogout }: DatabaseSelectorProps) {
  const [databases, setDatabases] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    loadDatabases();
  }, []);

  const loadDatabases = async () => {
    try {
      setLoading(true);
      const results = await notionService.getDatabases();
      setDatabases(results);
    } catch (err: any) {
      console.error('Database load error:', err);
      setError(err.message || 'Failed to load databases. Please check your API key.');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center space-y-4">
          <Loader2 className="h-10 w-10 animate-spin text-[var(--primary)] mx-auto" />
          <p className="text-[var(--muted-foreground)]">Loading your databases...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center p-8">
        <div className="card p-8 max-w-md w-full text-center space-y-4">
          <div className="w-16 h-16 bg-red-100 dark:bg-red-900/20 rounded-full flex items-center justify-center mx-auto">
            <Database className="h-8 w-8 text-red-600 dark:text-red-400" />
          </div>
          <h3 className="text-lg font-semibold">Connection Error</h3>
          <p className="text-sm text-[var(--muted-foreground)]">{error}</p>
          <button
            onClick={loadDatabases}
            className="button-primary px-6 py-2 mx-auto"
          >
            Try Again
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-[var(--background)]">
      {/* Header with Logout */}
      <header className="border-b border-[var(--border)] bg-[var(--card)]">
        <div className="max-w-6xl mx-auto px-6 py-4 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <Database className="h-6 w-6 text-[var(--primary)]" />
            <span className="font-semibold text-lg">Notion Viewer</span>
          </div>
          <button
            onClick={onLogout}
            className="button-secondary px-4 py-2 inline-flex items-center gap-2"
          >
            <LogOut className="h-4 w-4" />
            Logout
          </button>
        </div>
      </header>

      <div className="max-w-6xl mx-auto px-6 py-12">
        {/* Header */}
        <div className="text-center mb-12 space-y-4">
          <h1 className="text-4xl font-bold bg-gradient-to-r from-indigo-600 to-purple-600 dark:from-indigo-400 dark:to-purple-400 bg-clip-text text-transparent">
            Your Notion Databases
          </h1>
          <p className="text-lg text-[var(--muted-foreground)] max-w-2xl mx-auto">
            Select a database to explore its contents. All data is read-only with functional buttons preserved.
          </p>
        </div>

        {databases.length === 0 ? (
          <div className="card p-16 text-center space-y-4">
            <Database className="h-16 w-16 text-[var(--muted-foreground)] mx-auto" />
            <h3 className="text-xl font-semibold">No Databases Found</h3>
            <p className="text-[var(--muted-foreground)] max-w-md mx-auto">
              Make sure your Notion integration has access to at least one database. 
              You can share databases with your integration in Notion.
            </p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {databases.map((db: any) => {
              const icon = db.icon?.emoji || 'ðŸ“Š';
              const title = db.title[0]?.plain_text || 'Untitled Database';
              const lastEdited = new Date(db.last_edited_time);
              
              return (
                <button
                  key={db.id}
                  onClick={() => onSelect(db.id)}
                  className="card card-hover p-6 text-left group"
                >
                  <div className="space-y-4">
                    {/* Icon and Title */}
                    <div className="flex items-start justify-between">
                      <div className="flex items-center gap-3">
                        <span className="text-3xl">{icon}</span>
                        <h3 className="text-lg font-semibold line-clamp-2 group-hover:text-[var(--primary)] transition-colors">
                          {title}
                        </h3>
                      </div>
                      <ChevronRight className="w-5 h-5 text-[var(--muted-foreground)] group-hover:text-[var(--primary)] transition-all group-hover:translate-x-1" />
                    </div>
                    
                    {/* Metadata */}
                    <div className="space-y-2 text-sm text-[var(--muted-foreground)]">
                      <div className="flex items-center gap-2">
                        <Calendar className="w-4 h-4" />
                        <span>Created {new Date(db.created_time).toLocaleDateString('en-US', {
                          month: 'short',
                          day: 'numeric',
                          year: 'numeric'
                        })}</span>
                      </div>
                      <div className="flex items-center gap-2">
                        <Clock className="w-4 h-4" />
                        <span>Updated {getRelativeTime(lastEdited)}</span>
                      </div>
                    </div>
                    
                    {/* Visual Indicator */}
                    <div className="pt-2">
                      <div className="h-1 bg-gradient-to-r from-indigo-500 to-purple-500 rounded-full opacity-0 group-hover:opacity-100 transition-opacity" />
                    </div>
                  </div>
                </button>
              );
            })}
          </div>
        )}
      </div>
    </div>
  );
}

function getRelativeTime(date: Date): string {
  const now = new Date();
  const diffInSeconds = Math.floor((now.getTime() - date.getTime()) / 1000);
  
  if (diffInSeconds < 60) return 'just now';
  if (diffInSeconds < 3600) return `${Math.floor(diffInSeconds / 60)} minutes ago`;
  if (diffInSeconds < 86400) return `${Math.floor(diffInSeconds / 3600)} hours ago`;
  if (diffInSeconds < 604800) return `${Math.floor(diffInSeconds / 86400)} days ago`;
  
  return date.toLocaleDateString('en-US', {
    month: 'short',
    day: 'numeric',
    year: date.getFullYear() !== now.getFullYear() ? 'numeric' : undefined
  });
}