'use client';

import { Calendar, Link2, Mail, Phone, User, FileText, ChevronDown, Hash, CheckSquare, Square } from 'lucide-react';

interface PropertyRendererProps {
  property: any;
}

// Notion color mappings
const notionColors: Record<string, { bg: string; text: string }> = {
  default: { bg: 'bg-gray-100 dark:bg-gray-800', text: 'text-gray-700 dark:text-gray-300' },
  gray: { bg: 'bg-gray-100 dark:bg-gray-800', text: 'text-gray-700 dark:text-gray-300' },
  brown: { bg: 'bg-amber-100 dark:bg-amber-900/30', text: 'text-amber-800 dark:text-amber-200' },
  orange: { bg: 'bg-orange-100 dark:bg-orange-900/30', text: 'text-orange-800 dark:text-orange-200' },
  yellow: { bg: 'bg-yellow-100 dark:bg-yellow-900/30', text: 'text-yellow-800 dark:text-yellow-200' },
  green: { bg: 'bg-emerald-100 dark:bg-emerald-900/30', text: 'text-emerald-800 dark:text-emerald-200' },
  blue: { bg: 'bg-blue-100 dark:bg-blue-900/30', text: 'text-blue-800 dark:text-blue-200' },
  purple: { bg: 'bg-purple-100 dark:bg-purple-900/30', text: 'text-purple-800 dark:text-purple-200' },
  pink: { bg: 'bg-pink-100 dark:bg-pink-900/30', text: 'text-pink-800 dark:text-pink-200' },
  red: { bg: 'bg-red-100 dark:bg-red-900/30', text: 'text-red-800 dark:text-red-200' },
};

export default function PropertyRenderer({ property }: PropertyRendererProps) {
  if (!property) return null;

  switch (property.type) {
    case 'title':
      return (
        <span className="font-medium text-gray-900 dark:text-gray-100">
          {property.title[0]?.plain_text || ''}
        </span>
      );
    
    case 'rich_text':
      return (
        <span className="text-gray-700 dark:text-gray-300">
          {property.rich_text[0]?.plain_text || ''}
        </span>
      );
    
    case 'number':
      return (
        <div className="inline-flex items-center gap-1">
          <Hash className="h-3 w-3 text-gray-400" />
          <span className="font-mono text-sm">{property.number?.toLocaleString() || '0'}</span>
        </div>
      );
    
    case 'select':
      if (!property.select) return <span className="text-gray-400">â€”</span>;
      const selectColor = notionColors[property.select.color] || notionColors.default;
      return (
        <div className="inline-flex items-center">
          <span className={`inline-flex items-center gap-1.5 px-2 py-0.5 rounded-md text-xs font-medium ${selectColor.bg} ${selectColor.text}`}>
            <span>{property.select.name}</span>
            <ChevronDown className="h-3 w-3 opacity-50" />
          </span>
        </div>
      );
    
    case 'multi_select':
      return (
        <div className="inline-flex flex-wrap gap-1">
          {property.multi_select.map((option: any) => {
            const color = notionColors[option.color] || notionColors.default;
            return (
              <span
                key={option.id}
                className={`inline-flex items-center px-2 py-0.5 rounded-md text-xs font-medium ${color.bg} ${color.text}`}
              >
                {option.name}
              </span>
            );
          })}
        </div>
      );
    
    case 'date':
      if (!property.date) return <span className="text-gray-400">â€”</span>;
      const date = new Date(property.date.start);
      return (
        <div className="inline-flex items-center gap-1.5 text-sm">
          <Calendar className="h-3.5 w-3.5 text-gray-400" />
          <span>{date.toLocaleDateString('en-US', { 
            month: 'short', 
            day: 'numeric', 
            year: 'numeric' 
          })}</span>
        </div>
      );
    
    case 'checkbox':
      return (
        <div className="flex items-center">
          {property.checkbox ? (
            <CheckSquare className="h-4 w-4 text-blue-600 dark:text-blue-400" />
          ) : (
            <Square className="h-4 w-4 text-gray-400" />
          )}
        </div>
      );
    
    case 'url':
      if (!property.url) return <span className="text-gray-400">â€”</span>;
      return (
        <a
          href={property.url}
          target="_blank"
          rel="noopener noreferrer"
          className="inline-flex items-center gap-1 text-blue-600 hover:text-blue-800 dark:text-blue-400 dark:hover:text-blue-300 hover:underline"
          onClick={(e) => e.stopPropagation()}
        >
          <Link2 className="h-3.5 w-3.5" />
          <span className="text-sm">Link</span>
        </a>
      );
    
    case 'email':
      if (!property.email) return <span className="text-gray-400">â€”</span>;
      return (
        <a
          href={`mailto:${property.email}`}
          className="inline-flex items-center gap-1 text-blue-600 hover:text-blue-800 dark:text-blue-400 dark:hover:text-blue-300 hover:underline"
          onClick={(e) => e.stopPropagation()}
        >
          <Mail className="h-3.5 w-3.5" />
          <span className="text-sm">{property.email}</span>
        </a>
      );
    
    case 'phone_number':
      if (!property.phone_number) return <span className="text-gray-400">â€”</span>;
      return (
        <div className="inline-flex items-center gap-1 text-sm">
          <Phone className="h-3.5 w-3.5 text-gray-400" />
          <span>{property.phone_number}</span>
        </div>
      );
    
    case 'status':
      if (!property.status) return <span className="text-gray-400">â€”</span>;
      const statusColor = notionColors[property.status.color] || notionColors.default;
      return (
        <span className={`inline-flex items-center px-2.5 py-1 rounded-md text-xs font-medium ${statusColor.bg} ${statusColor.text}`}>
          <span className="w-1.5 h-1.5 rounded-full bg-current mr-1.5" />
          {property.status.name}
        </span>
      );
    
    case 'button':
      return (
        <button
          onClick={(e) => {
            e.stopPropagation();
            if (property.button?.url) {
              window.open(property.button.url, '_blank');
            }
          }}
          className="inline-flex items-center px-3 py-1.5 bg-blue-600 hover:bg-blue-700 dark:bg-blue-500 dark:hover:bg-blue-600 text-white text-sm font-medium rounded-md transition-colors shadow-sm hover:shadow"
        >
          Click here
        </button>
      );
    
    case 'people':
      return (
        <div className="inline-flex items-center -space-x-2">
          {property.people.map((person: any) => (
            <div
              key={person.id}
              className="inline-flex items-center justify-center w-7 h-7 rounded-full bg-gray-200 dark:bg-gray-700 ring-2 ring-white dark:ring-gray-800"
              title={person.name}
            >
              {person.avatar_url ? (
                <img
                  src={person.avatar_url}
                  alt={person.name}
                  className="w-full h-full rounded-full object-cover"
                />
              ) : (
                <User className="h-4 w-4 text-gray-600 dark:text-gray-400" />
              )}
            </div>
          ))}
        </div>
      );
    
    case 'files':
      return (
        <div className="inline-flex flex-wrap gap-1">
          {property.files.map((file: any, index: number) => (
            <a
              key={index}
              href={file.external?.url || file.file?.url}
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center gap-1 px-2 py-0.5 bg-gray-100 dark:bg-gray-800 rounded text-xs hover:bg-gray-200 dark:hover:bg-gray-700 transition-colors"
              onClick={(e) => e.stopPropagation()}
            >
              <FileText className="h-3 w-3" />
              <span>{file.name || 'File'}</span>
            </a>
          ))}
        </div>
      );
    
    case 'created_time':
      return (
        <div className="inline-flex items-center gap-1.5 text-sm text-gray-500">
          <Calendar className="h-3.5 w-3.5" />
          <span>{new Date(property.created_time).toLocaleDateString('en-US', {
            month: 'short',
            day: 'numeric',
            year: 'numeric'
          })}</span>
        </div>
      );
    
    case 'created_by':
      return (
        <div className="inline-flex items-center gap-1.5 text-sm">
          <div className="w-5 h-5 rounded-full bg-gray-200 dark:bg-gray-700 flex items-center justify-center">
            <User className="h-3 w-3 text-gray-600 dark:text-gray-400" />
          </div>
          <span className="text-gray-700 dark:text-gray-300">
            {property.created_by.name || property.created_by.id}
          </span>
        </div>
      );
    
    case 'last_edited_time':
      return (
        <div className="inline-flex items-center gap-1.5 text-sm text-gray-500">
          <Calendar className="h-3.5 w-3.5" />
          <span>{new Date(property.last_edited_time).toLocaleDateString('en-US', {
            month: 'short',
            day: 'numeric',
            year: 'numeric'
          })}</span>
        </div>
      );
    
    case 'last_edited_by':
      return (
        <div className="inline-flex items-center gap-1.5 text-sm">
          <div className="w-5 h-5 rounded-full bg-gray-200 dark:bg-gray-700 flex items-center justify-center">
            <User className="h-3 w-3 text-gray-600 dark:text-gray-400" />
          </div>
          <span className="text-gray-700 dark:text-gray-300">
            {property.last_edited_by.name || property.last_edited_by.id}
          </span>
        </div>
      );
    
    default:
      return (
        <span className="text-gray-400 text-sm">
          {JSON.stringify(property).slice(0, 50)}...
        </span>
      );
  }
}