'use client';

import { useState } from 'react';
import { KeyRound, Shield, ExternalLink, Eye, EyeOff } from 'lucide-react';

interface ApiKeyInputProps {
  onSubmit: (apiKey: string, remember: boolean) => void;
}

export default function ApiKeyInput({ onSubmit }: ApiKeyInputProps) {
  const [apiKey, setApiKey] = useState('');
  const [error, setError] = useState('');
  const [showKey, setShowKey] = useState(false);
  const [rememberMe, setRememberMe] = useState(true);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!apiKey.trim()) {
      setError('Please enter an API key');
      return;
    }
    // Notion API keys can start with different prefixes
    if (!apiKey.startsWith('secret_') && !apiKey.startsWith('ntn_')) {
      setError('Invalid API key format');
      return;
    }
    onSubmit(apiKey.trim(), rememberMe);
  };

  return (
    <div className="min-h-screen flex items-center justify-center p-8 bg-gradient-to-br from-indigo-50 via-white to-purple-50 dark:from-gray-900 dark:via-gray-900 dark:to-gray-900">
      <div className="card max-w-md w-full p-8 space-y-8">
        {/* Header */}
        <div className="text-center space-y-2">
          <div className="w-16 h-16 bg-gradient-to-br from-indigo-500 to-purple-600 rounded-2xl flex items-center justify-center mx-auto shadow-lg">
            <KeyRound className="h-8 w-8 text-white" />
          </div>
          <h1 className="text-2xl font-bold">Connect to Notion</h1>
          <p className="text-[var(--muted-foreground)]">
            Enter your Notion integration token to get started
          </p>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="space-y-2">
            <label htmlFor="api-key" className="block text-sm font-medium">
              API Key
            </label>
            <div className="relative">
              <input
                id="api-key"
                name="api-key"
                type={showKey ? 'text' : 'password'}
                value={apiKey}
                onChange={(e) => {
                  setApiKey(e.target.value);
                  setError('');
                }}
                className="w-full px-4 py-3 pr-12 border border-[var(--border)] rounded-lg bg-[var(--card)] focus:outline-none focus:ring-2 focus:ring-[var(--primary)] focus:border-transparent transition-all"
                placeholder="secret_..."
                autoComplete="off"
              />
              <button
                type="button"
                onClick={() => setShowKey(!showKey)}
                className="absolute right-3 top-1/2 -translate-y-1/2 p-1 hover:bg-[var(--muted)] rounded transition-colors"
              >
                {showKey ? (
                  <EyeOff className="h-5 w-5 text-[var(--muted-foreground)]" />
                ) : (
                  <Eye className="h-5 w-5 text-[var(--muted-foreground)]" />
                )}
              </button>
            </div>
            {error && (
              <p className="text-sm text-red-600 dark:text-red-400">{error}</p>
            )}
          </div>

          {/* Remember Me Checkbox */}
          <label className="flex items-center gap-2 cursor-pointer">
            <input
              type="checkbox"
              checked={rememberMe}
              onChange={(e) => setRememberMe(e.target.checked)}
              className="checkbox-modern"
            />
            <span className="text-sm font-medium">Remember me</span>
          </label>

          <button
            type="submit"
            className="button-primary w-full py-3 text-base"
          >
            Connect to Notion
          </button>
        </form>

        {/* Security Note */}
        <div className="bg-[var(--muted)] rounded-lg p-4 space-y-2">
          <div className="flex items-center gap-2 text-sm font-medium">
            <Shield className="h-4 w-4 text-[var(--primary)]" />
            Security Note
          </div>
          <p className="text-sm text-[var(--muted-foreground)]">
            Your API key is only used to connect to your Notion workspace. 
            It's never stored on any server.
          </p>
        </div>

        {/* Help Link */}
        <div className="text-center">
          <a
            href="https://www.notion.so/my-integrations"
            target="_blank"
            rel="noopener noreferrer"
            className="inline-flex items-center gap-2 text-sm text-[var(--primary)] hover:underline"
          >
            Create a Notion integration
            <ExternalLink className="h-3 w-3" />
          </a>
        </div>
      </div>
    </div>
  );
}